    </el-steps>
  </div>
</template>

<script>
  import PresentableSteps from './index'
  export default PresentableSteps
</script>

<style lang="less" scoped>
 .presentable-steps {
   position: relative;
   left: 50%;
   transform: translate(-25%,0);

   text-align: center;
   margin-bottom: 15px;
 }
</style>



